import React, { Component } from 'react';
import Grid from '@material-ui/core/Grid';
import styleCss from './CompanyDetail.css';
import PropTypes from 'prop-types';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import BackBtn from '../../../components/backBtn/Back';
import EditIcon from '@material-ui/icons/BorderColor';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import TextField from '@material-ui/core/TextField';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import states from './CompanyDetail.state';
import CommnetIcon from '@material-ui/icons/ModeComment';
// customize default theme
const styles = theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    padding:15
  },
  textField:{
    border: '1px solid rgba(0, 0, 0, 0.42)',
borderBottom:'none',
  },
  tabsRoot: {
    borderBottom: '1px solid #fff',
  },
  tabsIndicator: {
    backgroundColor: '#fff',
    height: 0
  },
calToActiontabsIndicator:{
borderBottom:'2px solid #3040ca'
  },
  calToActionTabRoot: {
    textTransform: 'initial',
    fontWeight: theme.typography.fontWeightRegular,
    minWidth: 0,
    minHeight: 0,
    '&:hover': {
      color: '#40a9ff',
      opacity: 1,
    },
    '&$tabSelected': {
      color: '#3040ca',
      borderBottom: '3px solid #3040ca',
      fontWeight: theme.typography.fontWeightMedium,
    },
    '&:focus': {
      // color: '#40a9ff',
    },
  },
  labelContainer:{
    padding:5
  },
  tabRoot: {
    textTransform: 'initial',
    fontWeight: theme.typography.fontWeightRegular,
    background: '#f8f8f8',
    borderTopLeftRadius: 6,
    borderTopRightRadius: 6,
    minWidth: 110,
    fontSize:14,
    '&:hover': {
      color: '#40a9ff',
      opacity: 1,
    },
    '&$tabSelected': {
      color: '#142075',
      background: '#ffffff',
      boxShadow: '0px -8px 34px -6px rgba(214,211,214,1)',
      borderBottom: 'none',
      fontWeight: theme.typography.fontWeightMedium,
    },
    '&:focus': {
      // color: '#40a9ff',
    },
  },
  scrollButtons: {
    flex: '0 0 0px'
  },
  tabSelected: {},
});
// tabs container initilizer 
function TabContainer(props) {
  return (
    <Typography component="div" style={{ padding: 0, marginTop: '-1px' }}>
      {props.children}
    </Typography>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
};

class CompanyDetail extends Component {
  listDetail = ' ';
  state = states;
  handleChange = (event, value) => {
    this.setState({ value });
  };
  calToActionTabHandleChange=(event, value) => {
    this.setState({ calToActionTabValue:value });
  };

  inputChangeHandler = (event, key, secIdentifier) => {

    let OldSecData = { ...this.state.dummyData };
    OldSecData[key] = event.target.value;
  
    this.setState({
      dummyData: OldSecData,
      saveBtnEnable: true
    })
  }
  // sec save 
  saveSecHandler = (secIdentifier) => {
    let UpdatedSecData = { ...this.state.dummyData };
    let updatedState={
      selectedSecId: 0,
    }
    updatedState[secIdentifier]=UpdatedSecData;
this.setState(updatedState)
  }
  // cancel Sec Handler
  cancelEditSecHandler = (secIdentifier) => {
    let OldSecData = this.state[secIdentifier];
    this.setState({
      selectedSecId: 0,
      secIdentifier: OldSecData
    })
 
  }
  // Edit Sec 
  editDetailSec = (id, secIdentifier) => {
let NewdummyData={...this.state[secIdentifier]}
    this.setState({
      selectedSecId: id,
      dummyData: NewdummyData,
      saveBtnEnable: false
    })
  }

  // edit lead name
  editLeadName=()=>{
   let  updatededitLeadName={
      contendEditable:true
    }
this.setState({
  editLeadName:updatededitLeadName
})
  } 

// save lead name  
saveLeadName=()=>{
  let  updatededitLeadName={
    contendEditable:false
  }
this.setState({
editLeadName:updatededitLeadName
})
}
// show messages
toggleMessages=($event)=>{
  this.setState({
    showComments: $event.target.checked
  })
}

  render() {
    const { classes } = this.props;
    const { value, calToActionTabValue} = this.state;
     return (
      <React.Fragment>
<h2 className={styleCss.title}> <BackBtn style={{width:35}} /> <div onBlur={()=>this.saveLeadName()}  contentEditable={this.state.editLeadName['contendEditable']}>Amalqamated Food Distributers</div> <EditIcon onClick={()=>this.editLeadName()} style={{ width: 16, marginLeft:15, cursor:'pointer' }} /></h2>
 <h3 className={styleCss.title}>Christchurch</h3>       
 <Tabs
          value={value}
          onChange={this.handleChange}
          indicatorColor="primary"
          textColor="primary"
          scrollable
          scrollButtons="auto"
          classes={{ root: classes.tabsRoot, indicator: classes.tabsIndicator, scrollButtons: classes.scrollButtons }}
        >
          <Tab label="Company details" classes={{ root: classes.tabRoot, selected: classes.tabSelected }} />
          <Tab label="Contact" classes={{ root: classes.tabRoot, selected: classes.tabSelected }} />
          <Tab label="Settings" classes={{ root: classes.tabRoot, selected: classes.tabSelected }} />
          <Tab label="Leads" classes={{ root: classes.tabRoot, selected: classes.tabSelected }} />
          <Tab label="Job" classes={{ root: classes.tabRoot, selected: classes.tabSelected }} />
          <Tab label="Action" classes={{ root: classes.tabRoot, selected: classes.tabSelected }} />
          <Tab label="Sales Data" classes={{ root: classes.tabRoot, selected: classes.tabSelected }} />
                 </Tabs>

        <Paper square={true} className={classes.root}>
          {value === 0 && <TabContainer>

            <Grid
              container
              direction="row"
              justify="center"
              alignItems="flex-start"
            >

              <Grid item xs={12} sm={6} style={{ marginBottom: 50 }}>
                <div>
                  <h4 onClick={this.editDetailSec.bind(this, 1, 'companyDetails')}>Company Details<EditIcon style={{ width: 16 }} /></h4>

                  {Object.keys(this.state.companyDetails).map((key) => {
                    return (
                      <div
                        key={key}>
                        <Grid
                          container
                          direction="row"
                          justify="flex-start"
                          alignItems="flex-start"
                        >
                          <Grid item xs={4} sm={3} style={{ marginBottom: 10 }}> {key}: </Grid>
                          <Grid item xs={8} sm={8} style={{ marginBottom: 10 }}>{this.state.selectedSecId === 1 ?
                            <FormControl fullWidth className={classes.margin}>
                              <Input
                                placeholder={this.state.companyDetails[key]}
                                onChange={($event) => this.inputChangeHandler($event, key, "companyDetails")} />
                            </FormControl> : `${this.state.companyDetails[key]}`}
                          </Grid>
                        </Grid>

                      </div>
                    );
                  })}
                  
                  {this.state.selectedSecId === 1 ?
                    <button className={"primaryRoundedBtn ml0 mt10"} disabled={!this.state.saveBtnEnable} onClick={() => this.saveSecHandler("companyDetails")}>Save</button> : ''}
                  {this.state.selectedSecId === 1 ? <button className={" primaryRoundedBtn mt10 cancelRoundedBtn"} onClick={() => this.cancelEditSecHandler("companyDetails")}>Cancel</button> : ''}

                </div>
              </Grid>

              <Grid item xs={12} sm={6} style={{ marginBottom: 50 }}>
                <div>

                  <h4 onClick={this.editDetailSec.bind(this, 2, 'invoiceDetails')}>Invoice Details<EditIcon style={{ width: 16 }} /></h4>

                  {Object.keys(this.state.invoiceDetails).map((key) => {
                    return (
                      <div
                        key={key}>
                        <Grid
                          container
                          direction="row"
                          justify="flex-start"
                          alignItems="flex-start"
                        >
                          <Grid item xs={4} sm={3} style={{ marginBottom: 10 }}> {key}: </Grid>
                          <Grid item xs={8} sm={8} style={{ marginBottom: 10 }}>  {this.state.selectedSecId === 2 ?
                            <FormControl fullWidth className={classes.margin}>
                              <Input  placeholder={this.state.invoiceDetails[key].toString()} 
                                onChange={($event) => this.inputChangeHandler($event, key, "invoiceDetails")} />
                            </FormControl> : `${this.state.invoiceDetails[key]}`}

                          </Grid>
                        </Grid>
                      </div>
                    );
                  })}

                  {this.state.selectedSecId === 2?
                    <button className={"primaryRoundedBtn ml0 mt10"} disabled={!this.state.saveBtnEnable} onClick={() => this.saveSecHandler("invoiceDetails")}>Save</button> : ''}
                  {this.state.selectedSecId === 2 ? <button className={" primaryRoundedBtn mt10 cancelRoundedBtn"} onClick={() => this.cancelEditSecHandler("invoiceDetails")}>Cancel</button> : ''}

                </div>
              </Grid>
            </Grid>
            <div className={styleCss.callToAction}>
            <hr/>
            <Tabs
            value={calToActionTabValue}
            onChange={this.calToActionTabHandleChange}
            indicatorColor="primary"
            textColor="primary"
  
            classes={{ root: classes.tabsRoot, indicator: classes.calToActiontabsIndicator, scrollButtons: classes.scrollButtons }}
          >
            <Tab label="Add Comment  " classes={{labelContainer:classes.labelContainer, root: classes.calToActionTabRoot, selected: classes.tabSelected }} />
            <Tab label="Add Call" classes={{ root: classes.calToActionTabRoot, selected: classes.tabSelected }} />
            <Tab label="Add Meeting" classes={{ root: classes.calToActionTabRoot, selected: classes.tabSelected }} />
         
            </Tabs>
            <div>
            {calToActionTabValue === 0 && <TabContainer><TextField
              id="filled-textarea"
             fullWidth
              placeholder="Add Comment"
              multiline
              rows="3"
              rowsMax="3"
              className={classes.textField}
             
              variant="outlined"
            />
            <button className={"primaryRoundedBtn ml0 mt10"}>Submit</button>
           <button className={" primaryRoundedBtn mt10 cancelRoundedBtn"}>Cancel</button>
           <div className="mt10">
           
           {this.state.showComments?<List>
          
             <ListItem style={{padding:'10px 0'}}>
               <ListItemIcon>
                 <CommnetIcon />
               </ListItemIcon>
               <ListItemText 
                 primary="It also doesn't matter how the element became focused"
                
               />
             </ListItem>
             <ListItem style={{padding:'10px 0'}}>
             <ListItemIcon>
               <CommnetIcon />
             </ListItemIcon>
             <ListItemText
               primary="Single-line It also doesn't matter how the element became focused"
              
             />
           </ListItem>
           <ListItem style={{padding:'10px 0'}}>
           <ListItemIcon>
             <CommnetIcon />
           </ListItemIcon>
           <ListItemText
             primary="Single-line item comment 2 It also doesn't matter how the element became focused"
            
           />
         </ListItem>
         <ListItem style={{padding:'10px 0'}}>
         <ListItemIcon>
           <CommnetIcon />
         </ListItemIcon>
         <ListItemText
           primary="It also doesn't matter how the element became focused"
          
         />
       </ListItem>
         </List>:''}
           
           </div>
           <div className="">
           <FormControlLabel
           control={
             <Checkbox onChange={($event)=>this.toggleMessages($event)} />
           }
           label= {!this.state.showComments?"Show all the Comments":"Hide Comments"}
         />
         </div>
            </TabContainer>}
            {calToActionTabValue === 1 && <TabContainer>Item 1</TabContainer>}
            {calToActionTabValue === 2 && <TabContainer>Item 2</TabContainer>}
            </div>
            </div>
          </TabContainer>}
          {value === 1 && <TabContainer>Item Two</TabContainer>}
          {value === 2 && <TabContainer>Item Three</TabContainer>}
          {value === 3 && <TabContainer>Item Four</TabContainer>}
          {value === 4 && <TabContainer>Item Five</TabContainer>}
          {value === 5 && <TabContainer>Item Six</TabContainer>}
          {value === 6 && <TabContainer>Item Seven</TabContainer>}

         
         

        </Paper>
      </React.Fragment>
    );
  }
}

export default withStyles(styles)(CompanyDetail);



